import { css } from "@emotion/react"
import { Link } from "gatsby"
import { colorScheme } from "../lib/color"
import { postStyle } from "../lib/post-style"
import Block from "./block"
import CommentBtn from "./comment-btn"
import IconBtn from "./icon-btn"
import LikeBtn from "./like-btn"

interface PostListProps {
  data: PostData[];
}

export default function PostList({ data }: PostListProps) {
  return(
    <div>
      {data.map(item => <PostItem data={item} key={item.id} />)}
    </div>
  )
}

export interface PostData {
  html: string;
  id: string;
  excerpt: string;
  frontmatter: {
    title?: string | null;
    description?: string | null;
  }
}

interface PostItemProps {
  data: PostData;
}

function PostItem({ data }: PostItemProps) {
  return(
    <Block>
      {data.frontmatter.title?
        <Link to={`/posts/${data.id}/`} css={styles.title}>
          <h1>{data.frontmatter.title}</h1>
        </Link>
      : null}
      <div
        css={postStyle}
        dangerouslySetInnerHTML={{__html: data.frontmatter.description? data.frontmatter.description : data.excerpt}}
      />
      <div css={styles.fbar}>
        <div css={css`display: flex;`}>
          <CommentBtn path={`/posts/${data.id}/`} />
          <LikeBtn path={`/posts/${data.id}/`} style={{marginLeft: 10}} />
        </div>
        <Link to={`/posts/${data.id}/`} css={css`text-decoration: none;`}>
          <IconBtn title="FULL TEXT" />
        </Link>
      </div>
    </Block>
  )
}

const styles = {
  title: css`
    color: inherit;
    text-decoration: none;
    :hover {
      text-decoration: underline;
    }
    h1 {
      font-size: 35px;
      font-weight: bold;
      margin: 0 0 15px 0;
    }
  `,
  fbar: css`
    display: flex;
    justify-content: space-between;
    margin-top: 20px;
  `
}